(function() {
  this.App.config(["$logProvider", "toastrConfig", function($logProvider, toastrConfig) {
    $logProvider.debugEnabled(true);
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = false;
    return toastrConfig.progressBar = true;
  }]).config(["$httpProvider", function($httpProvider) {
    return $httpProvider.interceptors.push(["$q", "$window", "$injector", "$log", function($q, $window, $injector, $log) {
      return {
        responseError: function(rejection) {
          var toastr;
          if (rejection.status === 401) {
            toastr = $injector.get('toastr');
            $window.location.href = "/";
            toastr.error("You are no longer connected or not an administrator, you will be redirected to the dashboard.");
            $log.error("User is not connected!");
          }
          return $q.reject(rejection);
        }
      };
    }]);
  }]).config(["$provide", function($provide) {
    return $provide.decorator('taOptions', [
      '$delegate', function(taOptions) {
        taOptions.toolbar = [['h1', 'h2', 'h3', 'p', 'quote'], ['bold', 'italics', 'underline', 'ul', 'ol'], ['html'], ['insertVideo', 'insertImage', 'insertLink'], ['undo', 'redo', 'clear'], ['wordcount', 'charcount']];
        return taOptions;
      }
    ]);
  }]).config(["$translateProvider", "LOCALES", function($translateProvider, LOCALES) {
    $translateProvider.useStaticFilesLoader({
      prefix: 'locales/',
      suffix: '.json'
    });
    $translateProvider.preferredLanguage(LOCALES.preferredLanguage);
    $translateProvider.fallbackLanguage(LOCALES.fallbackLanguage);
    $translateProvider.useMissingTranslationHandlerLog();
    $translateProvider.useSanitizeValueStrategy('sanitize');
    return $translateProvider.useMessageFormatInterpolation();
  }]).config(["$breadcrumbProvider", function($breadcrumbProvider) {
    return $breadcrumbProvider.setOptions({
      template: '<ol class="breadcrumb">\n  <li ng-repeat="step in steps" ng-class="{active: $last}" ng-switch="$last || !!step.abstract">\n    <a ng-switch-when="false" href="{{step.ncyBreadcrumbLink}}">{{step.ncyBreadcrumbLabel | translate}}</a>\n  <span ng-switch-when="true">{{step.ncyBreadcrumbLabel | translate}}</span>\n  </li>\n</ol>'
    });
  }]);

}).call(this);

