(function() {
  this.App.directive('currencyCentsInput', function() {
    return {
      require: 'ngModel',
      link: function(scope, element, attrs, ngModel) {
        ngModel.$parsers.push(function(value) {
          return parseInt(value * 100);
        });
        return ngModel.$formatters.push(function(value) {
          return value / 100;
        });
      }
    };
  });

}).call(this);

