(function() {
  this.App.controller('ProductController', ["$stateParams", "$state", "$timeout", "$document", "Upload", "MnoeProducts", "toastr", "MnoErrorsHandler", "CURRENCIES", function($stateParams, $state, $timeout, $document, Upload, MnoeProducts, toastr, MnoErrorsHandler, CURRENCIES) {
    'ngInject';
    var update, vm;
    vm = this;
    vm.product = {};
    vm.product.pricing_plans = [];
    vm.currencies = _.clone(CURRENCIES.values);
    vm.isLoading = true;
    MnoeProducts.get($stateParams.productId).then(function(response) {
      vm.product = response.data;
      vm.product.values_display = [];
      return _.each(vm.product.values_attributes, function(v) {
        return vm.product.values_display[v.name] = v.data;
      });
    })["finally"](function() {
      return vm.isLoading = false;
    });
    vm.updateStatus = function() {
      return update(['active']);
    };
    vm.updateProduct = function() {
      return update(['name', 'values_attributes']);
    };
    vm.saveFreeTrial = function() {
      return update(['free_trial_enabled', 'free_trial_duration', 'free_trial_unit']);
    };
    vm.addPricingPlan = function() {
      vm.pricingPlan = {
        name: '',
        description: '',
        free_trial_enabled: null,
        free_trial_duration: 0,
        per_duration: '',
        per_unit: '',
        prices: []
      };
      vm.currencies = _.clone(CURRENCIES.values);
      return vm.product.product_pricings.push(vm.pricingPlan);
    };
    vm.editPricingPlan = function(pricingPlan) {
      if (!!vm.currentPricingPlanId) {
        return;
      }
      vm.currentPricingPlanId = pricingPlan.id;
      return vm.currencies = _.difference(CURRENCIES.values, _.map(pricingPlan.prices, 'currency'));
    };
    vm.updateProductPricing = function() {
      return update(['product_pricings']).then(function(response) {
        angular.copy(response.data.product.product_pricings, vm.product.product_pricings);
        return vm.currentPricingPlanId = null;
      });
    };
    vm.cancelPricingPlan = function(pricingPlan) {
      vm.currentPricingPlanId = null;
      if (pricingPlan.id == null) {
        _.pull(vm.product.product_pricings, pricingPlan);
      }
      return vm.currencies = _.clone(CURRENCIES.values);
    };
    vm.deletePricingPlan = function(productPricing) {
      var pricingIndex;
      pricingIndex = vm.product.product_pricings.indexOf(productPricing);
      vm.product.product_pricings.splice(pricingIndex, 1);
      return vm.updateProductPricing();
    };
    vm.isCurrentPricingPlan = function(pricingPlan) {
      return !pricingPlan.id || pricingPlan.id === vm.currentPricingPlanId;
    };
    vm.addPrice = function(price, pricingPlan) {
      if (pricingPlan.prices == null) {
        pricingPlan.prices = [];
      }
      pricingPlan.prices.push(_.clone(price));
      _.remove(vm.currencies, function(c) {
        return c === price.currency;
      });
      price.currency = null;
      return price.price_cents = null;
    };
    vm.removePrice = function(price, pricingPlan) {
      _.remove(pricingPlan.prices, price);
      return vm.currencies.push(price.currency);
    };
    vm.uploadLogo = function(file, form) {
      file.upload = Upload.upload({
        headers: {
          'Accept': 'application/json'
        },
        url: "/mnoe/jpi/v1/admin/products/" + vm.product.id + "/upload_logo",
        data: {
          id: vm.product.id,
          image: file
        }
      });
      return file.upload.then(function(response) {
        $timeout(function() {
          file.result = true;
          return form.$setPristine();
        });
        return $timeout((function() {
          file.progress = -1;
        }), 3000);
      }, function(error) {
        MnoErrorsHandler.processServerError(error);
        if (error.status > 0) {
          return file.error = true;
        }
      }, function(evt) {
        return file.progress = parseInt(100.0 * evt.loaded / evt.total);
      });
    };
    vm.deleteLogo = function(asset) {
      return MnoeProducts.deleteAsset(asset);
    };
    update = function(params) {
      vm.product.values_attributes = _.map(_.keys(vm.product.values_display), function(k) {
        return {
          name: k,
          data: vm.product.values_display[k]
        };
      });
      return vm.product.patch(_.pick(vm.product, params)).then(function(response) {
        toastr.success('mnoe_admin_panel.dashboard.product.success', {
          extraData: {
            product: vm.product.name
          }
        });
        return response;
      }, function(error) {
        toastr.error('mnoe_admin_panel.dashboard.edit_product.error', {
          extraData: {
            organization_name: vm.organization.name
          }
        });
        return MnoErrorsHandler.processServerError(error, vm.form);
      });
    };
  }]);

}).call(this);

